export default {
	props: {
		urls: Array,
		size: {
			type: Number,
			default: 80
		},
		max: {
			type: Number,
			default: 5
		},
		shape: {
			type: String,
			default: 'circle'
		}
	},
	computed: {
		overLen() {
			return this.urls.length - this.max;
		}
	}
};
